<template>
  <div id="encyclopedias">


    <new-layout>
    <template slot="top">
     <div id="announce_header" >
        <i class="iconfont icon-fanhui" @click="$router.push('encyclopedias')"></i>
        <span class="tit text-overflow">
      <slot v-if="res.result.base.type==1">指标详情</slot>
      <slot v-if="res.result.base.type==2">疾病详情</slot>
    </span>
        <i style="opacity: 0;" class="iconfont icon-fanhui"></i>
      </div>
   </template>



      <template slot="center" style="display: none;">
     <header>{{res.result.base.name}}</header>

        <table class="fz24">
          <tr>
            <td class="width100 fz24">疾病名称：</td>
            <td>{{res.result.base.name}}</td>
          </tr>
          <tr>
            <td class="width100 fz24">所属科室：</td>
            <td>{{res.result.base.department}}</td>
          </tr>
          <tr>
            <td class="width100 fz24" v-html="disease"></td>
            <td v-if="res.result.base.type==1">{{res.result.base.inspectionPurpose}}</td>
            <td v-if="res.result.base.type==2">{{res.result.base.diseaseOverview}}</td>
          </tr>
          <tr>
            <td class="width100 fz24" v-html="classification"></td>
            <td  v-if="res.result.base.type==1">{{res.result.base.judgementStandard}}</td>
            <td  v-if="res.result.base.type==2">{{res.result.base.diseaseClassification}}</td>
          </tr>
        </table>
        <div class="disease_detail" v-if="zhibiaoxiangqing">
          疾病详情
        </div>
     <!--   <van-tabs v-model="active">
          <van-tab :title="item.title" v-for="(item,index) in  res.result.detail" :key="index">
              <high></high>
          </van-tab>
        </van-tabs>-->




        <van-tabs v-model="activeName1">
          <van-tab :title="item.title" :name="item.title" v-for="(item,index) in  newArray" :key="index">

            <van-collapse v-model="activeNames" accordion>
              <van-collapse-item :title="reason" name="1" v-if="dd_cArrayShow">

                <li class="sugges_li">
                 <div v-for="(items,indexs) in item.cause" :key="indexs">
                    <p class="title">{{items.key }}</p>
                    <p>{{items.value}}</p>
                  </div>
                </li>


              </van-collapse-item>


              <van-collapse-item title="症状" name="2" v-if="item.symptom">
                  <div v-for="(items,indexs) in item.symptom" :key="indexs">
                    <p class="title">{{items.key }}</p>
                    <p>{{items.value}}</p>
                  </div>

              </van-collapse-item>



              <van-collapse-item title="风险或并发症" name="3"  v-if="item.risk">

                  <div v-for="(items,indexs) in item.risk" :key="indexs">
                    <p class="title">{{items.key }}</p>
                    <p>{{items.value}}</p>
                  </div>

              </van-collapse-item>



                <van-collapse-item title="生活方式" name="5" v-if="item.lifestyle">
                    <div v-for="(items,indexs) in item.lifestyle" :key="indexs">
                      <p class="title">{{items.key }}</p>
                      <p>{{items.value}}</p>
                    </div>

                </van-collapse-item>

                <van-collapse-item title="饮食建议" name="6" v-if="item.dietaryAdvice">
                   <span class="suit" v-if="item.dietaryShouldEat">宜</span>
                    <div v-for="(items,indexs) in item.dietaryShouldEat" :key="indexs" v-if="item.dietaryShouldEat">
                      <p class="title">{{items.key }}</p>
                      <p>{{items.value}}</p>
                    </div>
                    <span class="avoid"  v-if="item.dietaryAvoidEat">忌</span>
                    <div v-for="(items,indexs) in item.dietaryAvoidEat" :key="indexs" v-if="item.dietaryAvoidEat">
                      <p class="title">{{items.key }}</p>
                      <p>{{items.value}}</p>
                    </div>
               </van-collapse-item>



                <van-collapse-item title="医疗保健" name="8"  v-if="item.medicalInsurance">
                   <div v-for="(items,indexs) in item.medicalInsurance" :key="indexs">
                      <p class="title">{{items.key }}</p>
                      <p>{{items.value}}</p>
                    </div>
               </van-collapse-item>


              <van-collapse-item title="运动建议" name="9" v-if="item.sportsAdvice">
                 <div v-for="(items,indexs) in item.sportsAdvice" :key="indexs">
                    <p class="title">{{items.key}}</p>
                    <p>{{items.value}}</p>
                  </div>
             </van-collapse-item>


              <van-collapse-item title="生活常识" name="10"  v-if="item.lifeCommonSense">

                  <div v-for="(items,indexs) in item.lifeCommonSense" :key="indexs">
                    <p class="title">{{items.key }}</p>
                    <p>{{items.value}}</p>
                  </div>

              </van-collapse-item>


              <van-collapse-item title="就医复查指南" name="11"  v-if="item.medicalReviewGuide">

                  <div v-for="(items,indexs) in item.medicalReviewGuide" :key="indexs">

                    <p class="title">{{items.key }}</p>
                    <p>{{items.value}}</p>
                  </div>

              </van-collapse-item>
            </van-collapse>

          </van-tab>

        </van-tabs>



        <!--选项卡判断-->

      </template>
    </new-layout>

  </div>
</template>

<script>
  import { Tab, Tabs } from 'vant';
  import high from '@/components/encyclopedias/high'
  import low from '@/components/encyclopedias/low'
  export default {
    data(){
      return {
        active: 2,
        activeName1:2,
        activeNames:['0'],
        res:'',
        dd_lArray:[],
        dd_dArray:[],
        dd_saArray:[],
        dd_mArray:[],
        dd_nArray:[],
        dd_cArray:[],//病因
        dd_sArray:[],
        dd_rArray:[],
        dd_gArray:[],
        dd_dArray1:[],
        disease:':',
        classification:'',
        reason:'',
        dd_cArrayShow:false,
        dd_sArrayShow:false,
        dd_rArrayShow:false,
        dd_lArrayShow:false,
        dd_dArrayShow:false,
        dd_dArray1Show:false,
        dd_mArrayShow:false,
        dd_saArrayShow:false,
        dd_nArrayShow:false,
        dd_gArrayShow:false,
        highcomponent:true,//
        zhibiaoxiangqing: false,
        piangaopiandi: false,
        gao: false,

        highlowArray: [
          {
            index:0,
            value: '偏高'
          },
          {
            index:1,
            value: '偏低'
          }
          ], //偏高偏低数组
        newArray: []
      }
    },
    components:{
      high,
      low,
      'van-tab':Tab,
      'van-tabs':Tabs,
    },
    methods:{
      highlow(index) {
          this.highcomponent = !this.highcomponent;
      },
      async init(){
        let _this=this;
        let key=this.$route.query.code;
        let resData= await this.$http.get(this.$HOST+'/open/xk/symptom?code='+key);

        this.res = resData

        if(this.res.result.base.type == 1) {
          this.disease = "检查目的";
          this.classification = "判定标准";
          this.reason = "可能原因";
        } else {
          this.disease = "疾病概述";
          this.classification = "疾病分类";
          this.reason = "病因";
        }

        if(!this.res.result.detail.cause) {
          this.dd_cArrayShow=true;
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].cause =JSON.parse(this.res.result.detail[i].cause)
          }
         }

        // 运动建议
        if(!this.res.result.detail.sportsAdvice){
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].sportsAdvice =JSON.parse(this.res.result.detail[i].sportsAdvice)
          }
          this.dd_saArrayShow=true;
         }


        if(!this.res.result.detail.symptom) {
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].symptom =JSON.parse(this.res.result.detail[i].symptom)
          }
        }

        if(!this.res.result.detail.lifestyle) {
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].lifestyle =JSON.parse(this.res.result.detail[i].lifestyle)
          }
         }
         if(!this.res.result.detail.dietaryShouldEat) {
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].dietaryShouldEat =JSON.parse(this.res.result.detail[i].dietaryShouldEat)
          }
         }

        if(!this.res.result.detail.dietaryAvoidEat) { // dietaryAvoidEat
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].dietaryAvoidEat =JSON.parse(this.res.result.detail[i].dietaryAvoidEat)
          }
        }
        if(!this.res.result.detail.medicalInsurance) { // medicalInsurance
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].medicalInsurance =JSON.parse(this.res.result.detail[i].medicalInsurance)
          }
         }
        if(!this.res.result.detail.lifeCommonSense) { //
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].lifeCommonSense =JSON.parse(this.res.result.detail[i].lifeCommonSense)
          }
         }

        if(!this.res.result.detail.risk) { // risk
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].risk =JSON.parse(this.res.result.detail[i].risk)
          }
         }

        if(!this.res.result.detail.items) { // medicalReviewGuide
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].medicalReviewGuide =JSON.parse(this.res.result.detail[i].medicalReviewGuide)
          }
         }

        this.newArray = this.res.result.detail

      }
  },
    created(){
      this.init();
    }
  }
</script>

<style scoped lang="scss">
  $height80: 80px;
  $bg-fff: #ffffff;
  #encyclopedias {
    background: #f3f3f3;
    .sugges_li {
      margin-bottom: 12px;
      padding: 10px;
    }
    .title {
      color: orange;
      font-size: 24px;
    }
    p:nth-child(2){
      font-size: 18px;
    }
  }

  table{
    width: 96%;
    margin: auto;
    tr{
      height: 50px;
      line-height: 50px;
      td{
        font-size: 18px;
        font-weight: bold;
      }
      td:nth-child(2){
        font-weight: 100;
        color: #333333;
      }
    }
  }
  .van-cell__title span{
    font-size: 18px!important;
  }
  .width100{
    width: 160px;
    vertical-align: -webkit-baseline-middle;
  }

  header{
    font-size: 30px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }
  .disease_detail{
     color: #089ec3;
    font-size: 15px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }

  .suit{
    width: 30px;
    height: 30px;
    background: #68ba8a;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }
  .avoid{
    width: 30px;
    height: 30px;
    background: #d95d5d;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }

  .fz24{
    font-size: 24px;
  }



  #encyclopedias p{
    font-size: 28px!important;
  }

  #announce_header{
    width: 100%;
    height: 120px;
    color: #FFF;

    text-align: center;
    background: #6EAFFF;
    display: flex;
    align-items: center;
    justify-content: center;

    .iconfont{
      flex: .15;
      font-size: 50px;
      display: flex;
      justify-content: center;
    }

    span{
      flex: 1;
      font-size: 35px;
    }
  }
/deep/.van-tabs__wrap{
    position: static!important;
  }

  table tr td{
    font-size: 30px;
  }
  .disease_detail{
    font-size: 30px;
  }
</style>



