<template>
      <van-collapse v-model="activeNames">
           <van-collapse-item :title="reason" name="1" v-if="dd_cArrayShow">

           <li v-for="(item,index) in dd_crray" class="sugges_li" :key="index">

              <div v-for="(items,indexs) in item.cause" :key="indexs">
               <p class="title">{{items.key }}</p>
               <p>{{items.value}}</p>
             </div>

          </li>

       </van-collapse-item>


        <van-collapse-item title="症状" name="2" v-if="dd_sArrayShow">
          <li v-for="(item,index) in dd_sArray" class="sugges_li" :key="index">
            <div v-for="(items,indexs) in item.symptom" :key="indexs">
              <p class="title">{{items.key }}</p>
              <p>{{items.value}}</p>
            </div>
          </li>
        </van-collapse-item>



        <van-collapse-item title="风险或并发症" name="3"  v-if="dd_rArrayShow">
          <li v-for="(item,index) in dd_rArray" class="sugges_li" :key="index">
            <div v-for="(items,indexs) in item.risk" :key="indexs">
              <p class="title">{{items.key }}</p>
              <p>{{items.value}}</p>
            </div>
           </li>
        </van-collapse-item>


        <van-collapse-item title="专家建议" name="4"  v-if="dd_lArrayShow">
          <van-collapse-item title="生活方式" name="5">
            <li v-for="(item,index) in dd_lArray" class="sugges_li" :key="index">
             <div v-for="(items,indexs) in item.lifestyle" :key="indexs">
                <p class="title">{{items.key }}</p>
                <p>{{items.value}}</p>
              </div>
            </li>
          </van-collapse-item>





          <van-collapse-item title="饮食建议" name="6">
            <span class="suit">宜</span>
            <li v-for="(item,index) in dd_dArray" class="sugges_li"   v-if="dd_dArrayShow" :key="index">
               <div v-for="(items,indexs) in item.dietaryShouldEat" :key="indexs">
                <p class="title">{{items.key }}</p>
                <p>{{items.value}}</p>
              </div>
            </li>

            <span class="avoid">忌</span>
            <li v-for="(item,index) in dd_dArray1" class="sugges_li" v-if="dd_dArray1Show" :key="index">
              <div v-for="(items,indexs) in item.dietaryAvoidEat" :key="indexs">
                <p class="title">{{items.key }}</p>
                <p>{{items.value}}</p>
              </div>
           </li>
        </van-collapse-item>



          <van-collapse-item title="医疗保健" name="8" v-if="dd_mArrayShow">
            <li v-for="(item,index) in dd_mArray" class="sugges_li" :key="index">
               <div v-for="(items,indexs) in item.medicalInsurance" :key="indexs">
                <p class="title">{{items.key }}</p>
                <p>{{items.value}}</p>
              </div>
            </li>
            </van-collapse-item>
        </van-collapse-item>



        <van-collapse-item title="运动建议" name="9" v-if="dd_saArrayShow">
         <li v-for="(item,index) in dd_saArray" class="sugges_li" :key="index">
             <div v-for="(items,indexs) in item.sportsAdvice" :key="indexs">
               {{items}}
              <p class="title">{{items.key}}:{{items.value}}&lt;!&ndash;<!--{{items[0]}}-->&ndash;&gt;</p>
              <p>{{items.value}}</p>
            </div>
          </li>
       </van-collapse-item>


        <van-collapse-item title="生活常识" name="10" v-if="dd_nArrayShow">
          <li v-for="(item,index) in dd_nArray" class="sugges_li" :key="index">
            <div v-for="(items,indexs) in item.lifeCommonSense" :key="indexs">
              <p class="title">{{items.key }}</p>
              <p>{{items.value}}</p>
            </div>
          </li>
         </van-collapse-item>


        <van-collapse-item title="就医复查指南" name="11" v-if="dd_gArrayShow" :key="index">
         <li v-for="(item,index) in dd_gArray" class="sugges_li" :key="index">
           <div v-for="(items,indexs) in item.medicalReviewGuide" :key="indexs">
             <p class="title">{{items.key }}</p>
             <p>{{items.value}}</p>
           </div>
          </li>
         </van-collapse-item>
      </van-collapse>

</template>

<script>
 export default {
    data(){
      return {
        activeNames:['0'],
        res:'',
        dd_crray:[],
        dd_lArray:[],
        dd_dArray:[],
        dd_saArray:[],
        dd_mArray:[],
        dd_nArray:[],
        dd_cArray:[],//病因
        dd_sArray:[],
        dd_rArray:[],
        dd_gArray:[],
        dd_dArray1:[],
        disease:':',
        classification:'',
        reason:'',
        dd_cArrayShow:false,
        dd_sArrayShow:false,
        dd_rArrayShow:false,
        dd_lArrayShow:false,
        dd_dArrayShow:false,
        dd_dArray1Show:false,
        dd_mArrayShow:false,
        dd_saArrayShow:false,
        dd_nArrayShow:false,
        dd_gArrayShow:false,
     }
    },

    methods:{

      async init(){
        let _this=this;
        let key=this.$route.query.code;
        let resData= await this.$http.get(this.$HOST+'/open/xk/symptom?code='+key);

        this.res = resData

        if(this.res.result.base.type == 'index') {
            this.disease = "检查目的";
            this.classification = "判定标准";
            this.reason = "可能原因";
        } else {
          this.disease = "疾病概述";
          this.classification = "疾病分类";
          this.reason = "病因";
        }




       if(this.res.result.detail) {
          this.dd_cArrayShow=true;
           for(var i=0;i<this.res.result.detail.length;i++){
             this.res.result.detail[i].cause =JSON.parse(this.res.result.detail[i].cause)
           }
          _this.dd_crray = _this.res.result.detail
           // this.common(this.res.result.detail[0].cause,_this.dd_lArray)
        }


    // 运动建议
        if(this.res.result.detail){
          for(var i=0;i<this.res.result.detail.length;i++){
            this.res.result.detail[i].sportsAdvice =JSON.parse(this.res.result.detail[i].sportsAdvice)
          }
            this.dd_saArrayShow=true;
            _this.dd_saArray = _this.res.result.detail
         }


     if(this.res.result.detail) {
       this.dd_sArrayShow=true;
       for(var i=0;i<this.res.result.detail.length;i++){
         this.res.result.detail[i].symptom =JSON.parse(this.res.result.detail[i].symptom)
       }
        _this.dd_saArray = _this.res.result.detail
     }

      if(this.res.result.detail) {
          this.dd_lArrayShow=true;
        for(var i=0;i<this.res.result.detail.length;i++){
          this.res.result.detail[i].lifestyle =JSON.parse(this.res.result.detail[i].lifestyle)
        }
          _this.dd_lArray = this.res.result.detail
        }






      if(this.res.result.detail) {
       this.dd_dArrayShow=true;
        for(var i=0;i<this.res.result.detail.length;i++){
          this.res.result.detail[i].dietaryShouldEat =JSON.parse(this.res.result.detail[i].dietaryShouldEat)
        }
        _this.dd_dArray =  this.res.result.detail
     }




     if(this.res.result.detail) { // dietaryAvoidEat
       this.dd_dArray1Show=true;
       for(var i=0;i<this.res.result.detail.length;i++){
         this.res.result.detail[i].dietaryAvoidEat =JSON.parse(this.res.result.detail[i].dietaryAvoidEat)
       }
       _this.dd_dArray1= this.res.result.detail

     }
     if(this.res.result.detail) { // medicalInsurance
       this.dd_mArrayShow=true;
       for(var i=0;i<this.res.result.detail.length;i++){
         this.res.result.detail[i].medicalInsurance =JSON.parse(this.res.result.detail[i].medicalInsurance)
       }
       _this.dd_mArray =  this.res.result.detail

     }
     if(this.res.result.detail) { //
       this.dd_nArrayShow=true;
       for(var i=0;i<this.res.result.detail.length;i++){
         this.res.result.detail[i].lifeCommonSense =JSON.parse(this.res.result.detail[i].lifeCommonSense)
       }
       _this.dd_nArray =  this.res.result.detail
     }

      if(this.res.result.detail) { // risk
       this.dd_rArrayShow=true;
        for(var i=0;i<this.res.result.detail.length;i++){
          this.res.result.detail[i].risk =JSON.parse(this.res.result.detail[i].risk)
        }
       _this.dd_rArray = this.res.result.detail
     }

     if(this.res.result.detail) { // medicalReviewGuide
       this.dd_gArrayShow=true;
       for(var i=0;i<this.res.result.detail.length;i++){
         this.res.result.detail[i].medicalReviewGuide =JSON.parse(this.res.result.detail[i].medicalReviewGuide)
       }
       _this.dd_gArray = this.res.result.detail
     }
    },

  },
    created(){
      this.init();
    }
  }
</script>

<style scoped lang="scss">
  $height80: 80px;
  $bg-fff: #ffffff;
  #encyclopedias {
    background: #f3f3f3;
    .sugges_li {
      border-bottom: 1px dashed #999999;
      margin-bottom: 12px;
      padding: 10px;
    }
    .title {
      color: orange;
      font-size: 24px;
    }
    p:nth-child(2){
      font-size: 18px;
    }
  }

  table{
    width: 96%;
    margin: auto;
    tr{
      height: 50px;
      line-height: 50px;
      td{
        font-size: 18px;
      }
      td:nth-child(2){
        font-weight: bold;
        color: #333333;
      }
    }
  }
  .van-cell__title span{
    font-size: 18px!important;
  }
  .width100{
    width: 150px;
    vertical-align: -webkit-baseline-middle;
  }

  header{
    font-size: 30px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }
  .disease_detail{
     color: #089ec3;
    font-size: 15px;
    background: #ffffff;
    text-indent: 18px;
    height: 80px;
    line-height: 80px;
    font-weight: bold;
  }

  .suit{
    width: 30px;
    height: 30px;
    background: #68ba8a;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }
  .avoid{
    width: 30px;
    height: 30px;
    background: #d95d5d;
    display: inline-block;
    text-align: center;
    line-height: 30px;
    color: #ffffff;
    font-size: 14px;
  }

  .fz24{
    font-size: 24px;
  }



  #encyclopedias p{
    font-size: 28px!important;
  }

  #announce_header{
    width: 100%;
    height: 120px;
    color: #FFF;

    text-align: center;
    background: #6EAFFF;
    display: flex;
    align-items: center;
    justify-content: center;

    .iconfont{
      flex: .15;
      font-size: 50px;
      display: flex;
      justify-content: center;
    }

    span{
      flex: 1;
      font-size: 35px;
    }
  }
  .black{
    color: #000000;
  }
  .fw{
    font-weight: bold;
  }
</style>



